<template>
    <div>
        <!-- Table All Supplier -->
        <vx-card>
        <vs-table stripe search pagination  max-items="10" :data="supplier">
            <template slot="header" class="mb-5">
                <h1 class="mr-2">Data Supplier</h1> 
                <vx-tooltip text="Tambah Supplier" style="display:inline">
                    <vs-button @click="btnAddSupplier" radius icon="create" color="success" type="flat" size="large"></vs-button>
                </vx-tooltip>
            </template>
            
            <template slot="thead">
                <vs-th sort-key="nama" class="bg-primary text-white">
                Nama Supplier
                </vs-th>
                <vs-th sort-key="pic_nama" class="bg-primary text-white">
                Nama PIC
                </vs-th>
                <vs-th sort-key="pic_hp" class="bg-primary text-white">
                No Handphone
                </vs-th>
                <vs-th sort-key="pic_email" class="bg-primary text-white">
                Email
                </vs-th>
                <vs-th sort-key="aksi" width="100px" class="bg-primary text-white text-center">
                <p class="w-full">Aksi</p>
                </vs-th>
            </template>

            <template slot-scope="{data}">
                <vs-tr :key="items.kode" v-for="items in data" >
                    <vs-td :data="items.nama">
                        {{items.nama}}
                    </vs-td>
                    <vs-td :data="items.pic_nama">
                        {{items.pic_nama}}
                    </vs-td>
                    <vs-td :data="items.pic_hp">
                        {{items.pic_hp}}
                    </vs-td>
                    <vs-td :data="items.pic_email">
                        {{items.pic_email}}
                    </vs-td>
                    <vs-td :data="items.aksi" class="text-center">
                    <vx-tooltip text="Ubah Supplier" style="display:inline">
                        <vs-button @click="editData(items.kode, items.nama, items.pic_nama, items.pic_hp, items.pic_email )" class="mr-1" color="warning" type="filled" size="small" icon="create" style="display:inline"></vs-button>
                    </vx-tooltip>
                    <vx-tooltip text="Ringkasan Pesanan" style="display:inline">
                        <vs-button @click="popupPoBtn(items.kode, items.nama)" color="#0fabbc" type="filled" size="small" icon="search" style="display:inline"></vs-button>
                    </vx-tooltip>
                    </vs-td>
                </vs-tr>
            </template>
        </vs-table>
        </vx-card>

        <!-- Add Supplier -->
        <vs-popup class="holamundo"  title="Tambah Supplier" :active.sync="addNew">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama Supplier" v-validate="'required'"  data-vv-validate-on="blur" name="form.nama" v-model="form.nama" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama PIC" v-validate="'required'"  data-vv-validate-on="blur" name="form.pic_nama" v-model="form.pic_nama" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="number" class="w-full" label="No Handphone" v-validate="'required|min:10'"  data-vv-validate-on="blur" name="form.pic_hp" v-model="form.pic_hp" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="email" class="w-full" label="Email" v-validate="'required'"  data-vv-validate-on="blur" name="form.pic_email" v-model="form.pic_email" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button color="success" :disabled="!validateFormAdd" class="mr-3 mb-2" @click=" addData()">Simpan</vs-button>
                    <!-- <vs-button color="warning" type="border" class="mb-2" @click="form.nama = form.pic_nama = form.pic_hp = form.pic_email = '' ">Reset</vs-button> -->
                    </div>
                </div>
        </vs-popup>

        <!-- Edit Supplier -->
        <vs-popup class="holamundo"  title="Ubah Supplier" :active.sync="Edit">
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama Supplier" v-validate="'required'" data-vv-validate-on="blur" name="form.nama" v-model="form.nama" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input class="w-full" label="Nama PIC" v-validate="'required'" data-vv-validate-on="blur" name="form.pic_nama" v-model="form.pic_nama" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="number" class="w-full" label="No Handphone" v-validate="'required|min:10'" data-vv-validate-on="blur" name="form.pic_hp" v-model="form.pic_hp" />
                    </div>
                </div>
                <div class="vx-row mb-6">
                    <div class="vx-col w-full">
                    <vs-input type="email" class="w-full" label="Email" v-validate="'required'" data-vv-validate-on="blur" name="form.pic_email" v-model="form.pic_email" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col w-full text-right">
                    <vs-button class="mr-3 mb-2" color="success" :disabled="!validateFormEdit" @click="updateData()">Simpan</vs-button>
                    <!-- <vs-button color="warning" type="border" class="mb-2" @click="nama = kategori= harga = stock = '' ">Reset</vs-button> -->
                    </div>
                </div>
        </vs-popup>

        <!-- Popup Data PO -->
        <vs-popup fullscreen title="Ringkasan Pesanan" :active.sync="popupPo">
            <vx-card class="mb-5">
                <div class="md:flex">
                    <div class="lg:w-32 mt-2 md:mt-2">
                        Tanggal
                    </div> 
                    <div class="lg:w-46 mt-4 md:mt-0">
                        <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="selectedDate" ></datepicker>
                    </div>   
                    <div class="mt-2 md:mt-0 md:ml-6">
                        <vs-button radius color="primary" icon="search" @click="loadDataServer(form.kode)"  style="display:inline; margin-right:30px;"></vs-button>
                    </div>
                    <vx-tooltip text="Export Excel" style="display:inline">   
                        <vs-button @click="tableToExcel('table', 'Pesanan Pembelian')" color="success" class="mr-2" type="filled" style="display:inline;" size="small"><i class="material-icons">description</i></vs-button>    
                    </vx-tooltip>
                    <vx-tooltip text="Export PDF" style="display:inline">
                        <vs-button @click="tableToPdf" color="danger" type="filled" size="small" style="display:inline;" ><i class="material-icons">insert_drive_file</i></vs-button>
                    </vx-tooltip>
                </div>
            </vx-card>

            <!-- Table Data PO -->
            <vx-card>
            <vs-table stripe :data="dataPo" style="margin-bottom:100px;">
                <template slot="thead">
                    <vs-th sort-key="sku" width="120px" class="bg-primary text-white">
                    Sku
                    </vs-th>
                    <vs-th sort-key="keterangan" class="bg-primary text-white">
                    Keterangan
                    </vs-th>
                    <vs-th sort-key="qty" width="80px"  class="bg-primary text-white">
                    Qty
                    </vs-th>
                </template>

                <template slot-scope="{data}">
                    <vs-tr :key="items.kode" v-for="items in data" >
                        <vs-td :data="items.sku">
                            {{items.sku}}
                        </vs-td>
                        <vs-td :data="items.keterangan">
                            {{items.keterangan}}
                        </vs-td>
                        <vs-td :data="items.qty">
                            {{items.qty}}
                        </vs-td>
                    </vs-tr>
                </template>
            </vs-table>
            </vx-card>

            <!-- Cetak Excel -->
            <table hidden ref="table" id="loremTable" summary="lorem ipsum sit amet" rules="groups" frame="hsides" >
                <thead>
                    <tr>
                        <th colspan="3" style="text-align:left"><h4>Rincian Pesanan Tanggal : {{ selectedDate | moment("DD-MM-YYYY") }}</h4></th>
                    </tr>
                    <tr>
                        <th colspan="3" style="text-align:left"><h4>{{form.nama}}</h4></th>
                    </tr>
                    <tr>
                        <th></th>
                    </tr>
                    <tr>
                        <th></th>
                    </tr>
                    <tr>
                        <th>Sku</th>
                        <th>Keterangan</th>
                        <th>Qty</th>
                    </tr>
                </thead>

                <tbody>
                    <tr :key="items.kode" v-for="items in dataPo">
                        <td>{{items.sku}}</td>
                        <td>{{items.keterangan}}</td>
                        <td>{{items.qty}}</td>
                    </tr>
                </tbody>
            </table>

            <!-- Cetak PDF -->
            <table hidden id="tablePDF" style="margin-top:50px">
                <thead style="margin-top:50px">
                    <tr class="bg-primary text-white">
                    <th>Sku</th>
                    <th>Keterangan</th>
                    <th>Qty</th>
                    </tr>
                </thead>
                <tbody>
                    <tr :key="items.kode" v-for="items in dataPo" >
                        <td>{{items.sku}}</td>
                        <td>{{items.keterangan}}</td>
                        <td>{{items.qty}}</td>
                    </tr>
                </tbody>
            </table>
        </vs-popup>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import jsPDF from 'jspdf';

export default {
    data() {
        return {
            uri :'data:application/vnd.ms-excel;base64,',
            template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
            base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
            format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
            addNew:false,
            Edit:false,
            popupPo:false,
            selectedDate:new Date(),
            supplier:[],
            dataPo:[],
            form: {
                kode:'',
                nama:'',
                pic_nama:'',
                pic_hp:'',
                pic_email:'',
            }

        }
    },

    components:{
        Datepicker,
        jsPDF
    },

    computed: {
        validateFormAdd() {
            return !this.errors.any() 
            && this.form.nama != '' 
            && this.form.pic_hp != '' 
            && this.form.pic_nama != ''  
            && this.form.pic_email != '';
        },

        validateFormEdit() {
            return !this.errors.any() 
            && this.form.nama != '' 
            && this.form.pic_hp != '' 
            && this.form.pic_nama != ''  
            && this.form.pic_email != '';
        },
    },

    created() {
       this.loadData();
    },

    methods:{
        tableToPdf(){
            const doc = new jsPDF();
           
            let year = new Intl.DateTimeFormat('id', { year: 'numeric' }).format(this.selectedDate)
            let month = new Intl.DateTimeFormat('id', { month: 'long' }).format(this.selectedDate)
            let date = new Intl.DateTimeFormat('id', { month: 'numeric' }).format(this.selectedDate)
            doc.text('Rincian Pesanan Tanggal ' + date + month + year, 15, 35)
            doc.text( this.form.nama,15,45 )
            doc.autoTable({ margin: {top: 50}, html: '#tablePDF' })

            doc.save('Pesanan.pdf')
        },

        tableToExcel(table, name){
           if (!table.nodeType) table = this.$refs.table
            var ctx = {worksheet: name || 'Worksheet', table: table.innerHTML}
            window.location.href = this.uri + this.base64(this.format(this.template, ctx))
        },

        btnAddSupplier(){
            this.addNew = true,
            this.form.nama = '',
            this.form.pic_nama = '',
            this.form.pic_hp = '',
            this.form.pic_email = ''
        },

        loadData(){
            this.$http.get('agen/web/supplier')
            .then(response => {
                this.supplier = response.data.message
                // console.log(response);
            })
            .catch(err=>{
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
        },

        addData(){
            this.$vs.loading();
            const postData = {
                nama: this.form.nama,
                pic_nama: this.form.pic_nama,
                pic_hp: this.form.pic_hp,
                pic_email: this.form.pic_email,
            }
   
            this.$http.post('agen/web/supplier/create', postData)
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.form.nama = '',
                    this.form.pic_nama = '',
                    this.form.pic_hp = '',
                    this.form.pic_email = '',
                    this.addNew = false

                    this.$swal.fire({
                    title: 'Success',
                    text:  response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()})
        },

        editData(kode, nama, pic_nama, pic_hp, pic_email){
            this.Edit = true
            this.form.nama = nama
            this.form.pic_nama = pic_nama
            this.form.pic_hp = pic_hp
            this.form.pic_email = pic_email
        },

        updateData(){
            this.$vs.loading();
            const updtData = {
                kode: this.form.kode,
                nama: this.form.nama,
                pic_nama: this.form.pic_nama,
                pic_hp: this.form.pic_hp,
                pic_email: this.form.pic_email,
            }

           this.$http.post('agen/web/supplier/update', updtData)
            .then(response => {
                if(response.data.status == "SUKSES"){
                    this.form.nama = '',
                    this.form.pic_nama = '',
                    this.form.pic_hp = '',
                    this.form.pic_email = '',
                    this.Edit = false

                    this.$swal.fire({
                    title: 'Success',
                    text: response.data.message,
                    icon: 'success',

                    }).then(() => {
                        this.loadData();
                    })
                }else {
                    this.$swal.fire(
                        'Gagal !',
                        response.data.message,
                        'error'
                    )
                }
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()})
        },

        popupPoBtn(kode, nama){
            this.$vs.loading();
            this.popupPo =true
            this.form.kode = kode
            this.form.nama = nama

            const postData = {
                kode: kode,
                tanggal: this.selectedDate
            }
   
            this.$http.post('agen/web/supplier/po', postData)
            .then(response => {
                this.dataPo = response.data.message 
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()})
        },

        loadDataServer(kode){
            this.$vs.loading();
            const postData = {
                kode: kode,
                tanggal: this.selectedDate
            }
   
            this.$http.post('agen/web/supplier/po', postData)
            .then(response => {
                this.dataPo = response.data.message 
            })
            .catch(err=>{
                    this.$vs.loading.close()
                    this.$swal.fire(
                        'Gagal !',
                        err.response.data.message,
                        'error'
                        )
                    })
            .finally(() => {this.$vs.loading.close()})
        }
    }

}
</script>

<style lang="stylus">

    .swal2-container 
        z-index: 100000;
        
</style>